<template>
  <v-container class="cart-page-component">
    <v-stepper value="1" alt-labels>
      <v-stepper-header>
        <v-stepper-step step="1" class="text-uppercase">Summary</v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step step="2" class="text-uppercase">Shipping</v-stepper-step>
      </v-stepper-header>
    </v-stepper>
    <h3 class="text-uppercase headline">Cart</h3>
    <v-data-table
      :headers="headers"
      :items="cartItems"
      class="mb-4"
      hide-actions
    >
      <template slot="headers" slot-scope="props">
        <th v-for="item in props.headers" class="title text-xs-left">
          {{ item.text }}
        </th>
      </template>
      <template slot="items" slot-scope="props">
        <td class="subheading" >
          <v-layout row align-center >
            <v-flex >
              <ProductWithDesign
                :name="props.item.name"
                :product="props.item.product"
                :design="props.item.design"
                :currentColorIndex="props.item.currentColorIndex"
                :currentColorLightness="props.item.productVariant.color.lightness"
                class="product-img"
              />
            </v-flex>
          </v-layout>
        </td>
        <td class="text-xs-left">
          <v-layout column >
            <p class="title">{{ props.item.product.name }}</p>

            <p><span class="grey--text">Name: </span> <span class="text-capitalize">{{ props.item.name }}</span></p>
            <p><span class="grey--text">Design: </span> {{ props.item.design.name }}</p>
            <p><span class="grey--text">Color: </span>{{ props.item.productColor.name }}</p>
            <p><span class="grey--text">Size: </span> {{ props.item.productSize.name }}</p>
          </v-layout>
        </td>
        <td class="text-xs-center subheading">
          ${{
          (props.item.productColor && props.item.productColor.sizes && props.item.productColor.sizes[0].price)
          || props.item.productVariant.price
          }}
        </td>
        <td class="text-xs-center subheading">
          <div class="quantity-wrapper">
            <QuantityInput :value="props.item.count" @input="setItemCount(props.item, $event)"/>
          </div>
        </td>
        <td class="text-xs-center subheading">
          ${{ (parseFloat((props.item.productColor && props.item.productColor.sizes && props.item.productColor.sizes[0].price)
          || props.item.productVariant.price) * props.item.count).toFixed(2)  }}
        </td>
        <td>
          <v-btn @click="deleteCartItem(props.item)" class="error">Remove</v-btn>
        </td>
      </template>
      <template slot="no-data">
        <v-alert :value="true" color="red lighten-2" icon="warning">
          Cart is empty
        </v-alert>
      </template>
    </v-data-table>
    <p class="title text-xs-left font-weight-bold">Total: ${{ total }}</p>
    <div class="text-xs-left">
      <v-btn color="info"
             v-if="prevRouteName === 'product' || prevRouteName === 'products'"
             @click="$router.go(-1)"
      >
        Back
      </v-btn>
      <v-btn color="info" v-if="cartItems && cartItems.length" :to="{ name: 'specialShipping' }">Shipping</v-btn>
    </div>
  </v-container>
</template>

<script>
import Backend from '../services/backend';
import ProductWithDesign from "../components/ProductWithDesign";
import QuantityInput from '../components/QuantityInput';

const backend = new Backend();
const headers = [
  { text: '' },
  { text: 'Item' },
  { text: 'Price' },
  { text: 'Quantity' },
  { text: 'Total' },
  { text: '' }
];

export default {
  name: 'CartPage',
  components: { ProductWithDesign, QuantityInput },
  data() {
    return {
      headers: headers,
      prevRouteName: null
    };
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
        vm.prevRouteName = from.name
    })
  },
  computed: {
    cartItems() {
      return this.$store.state.cart.items;
    },
    total() {
      let total =  this.cartItems.reduce((accumulator, item) =>
         Number((accumulator + item.productVariant.price * item.count).toFixed(2))
      , 0);
      return (total).toFixed(2);
    }
  },
  methods: {
    deleteCartItem(item) {
      this.$store.dispatch('deleteCartItem', item);
    },
    setItemCount(item, count) {
      this.$store.dispatch('setCartItemCount', { item, count });
    },
  }
}
</script>

<style lang="scss">
.cart-page-component {
  p {
    font-size: 18px;
  }
  td, th {
    font-size: 18px !important;
    padding: 12px !important;
    .title {
      margin-bottom: 10px;
    }
    p {
      margin-bottom: 3px;
    }
  }
  .product-img {
    max-width: 100px;
    min-width: 50px;
  }
  .quantity-wrapper {
    display: inline-block;
    white-space: nowrap;
  }
}
</style>
