<template>
<v-container class="products-component">
  <v-layout column>
    <ProductCategories class="mb-5"/>
    <h3 class="text-uppercase display-2">Products</h3>
    <v-layout mt-4 row fill-height wrap :class="{'card-container-bordered': (products && products.length) }">
      <v-layout v-if="isLoading" justify-center row class="mt-4">
        <Preloader size="80"/>
      </v-layout>
      <v-layout v-else-if="!products || !products.length" justify-center row class="mt-4">
        <p>No products available in this category</p>
      </v-layout>
      <v-flex v-else xs12 sm6 md4 lg3 v-for="item in products" class="card">
        <v-card
          class="design-card"
          tile
          flat
          :to="{
            name: 'product',
            params: Object.assign({}, $route.params, {
              productCategory: $route.params.productCategory || 3,
              product: item.id
            })
          }"
        >
          <v-layout justify-center>
            <v-flex xs10>
              <ProductWithDesign
                :product="item"
                :design="currentDesign"
                :name="$route.params.name"
              />
            </v-flex>
          </v-layout>
          <span class="headline">{{ item.name }}</span>
        </v-card>
      </v-flex>
    </v-layout>
  </v-layout>

</v-container>
</template>

<script>
import ProductCategories from '../components/ProductCategories';
import Preloader from '../components/Layout/Preloader';
import ProductWithDesign from '../components/ProductWithDesign';

export default {
  name: 'ProductsPage',
  components: {
    ProductCategories,
    Preloader,
    ProductWithDesign
  },
  watch: {
    '$route.params': 'loadData'
  },
  created() {
    this.loadData();
  },
  computed: {
    products() {
      return this.$store.state.products.list.filter((item) => item.categories[0] && item.categories[0].id === this.productCategory);
    },
    isLoading() {
      return this.$store.state.productCategories.isLoading
        || this.$store.state.designs.isLoading
        || this.$store.state.products.isLoading;
    },
    currentDesign() {
      return this.$store.state.designs.current;
    },
    productCategory() {
        return parseInt(this.$route.params.productCategory) || 3;
    }
  },
  methods: {
    loadData() {
      this.$store.dispatch('fetchIfEmpty', { key: 'namesList'}).then((res) => {
          if (!this.$store.state.namesList.current) {
            this.$store.commit('setNameSlug', this.$route.params.name);
          }
      });
      this.$store.dispatch('fetchIfEmpty', { key: 'products', id: this.productCategory });
      this.$store.dispatch('fetchIfEmpty', { key: 'designs', id: this.$route.params.subcategory })
        .then(() => {
          this.$store.commit('setCurrentDesign', this.$route.params.design);
      });
    }
  }
};
</script>
<style lang="scss">
  .products-component {
    .image-wrapper {
      position: relative;
      display: inline-block;
    }
    .image-logo {
      position: absolute;
    }
    .image-product {
      width: 100%;
    }
    .design-card {
      cursor: pointer;
    }
    card-container-bordered {
      padding: 40px;
    }
  }
</style>
