<template>
  <v-container  class="product-categories-dialog-component">
    <v-layout justify-center>
        <h2 class="display-2 mb-2">Choose a Surface</h2>
    </v-layout>
    <v-layout row wrap justify-start class="card-container-bordered">
      <v-flex xs6 sm4 md3  v-for="item in productSubcategories" :key="item.slug" class="card">
        <v-card flat class="category-card ma-2"  @click="selectProductCategory(item.id)">
          <v-img :src="item.image"></v-img>
          <div class="mt-3">
            <span class="headline">{{ item.name }}</span>
          </div>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import Backend from '../services/backend';

const backend = new Backend();

export default {
  name: 'ProductCategoriesDialog',
  props: ['productSubcategories'],
  created() {
    this.$store.dispatch('fetchIfEmpty', { key: 'productCategories' });
  },
  methods: {
    selectProductCategory(productCategory) {
      this.$router.push({
        name: 'productCategory',
        params: {
          ...this.$router.params,
          productCategory: productCategory,
        }
      });
      this.closeDialog();
    },
    closeDialog() {
      this.$emit('closeDialog');
    }
  }
};
</script>

