<template>
  <div class="nameinput-component">
    <v-form>
      <div class="wrapper">
        <!--<v-combobox-->
          <!--class="autocomplete"-->
          <!--v-model="model"-->
          <!--:items="namesList"-->
          <!--:append-icon="null"-->
          <!--item-text="name"-->
          <!--item-value="slug"-->
          <!--placeholder="Type Your Name Here"-->
          <!--color="red"-->
          <!--@select="submit"-->
          <!--:counter="0"-->
          <!--hide-details-->
          <!--attach-->
          <!--flat-->
          <!--solo-->
          <!--:disabled="isLoading"-->
        <!--&gt;-->
          <!--&lt;!&ndash;@input.native="inp"&ndash;&gt;-->
        <!--</v-combobox>-->
        <v-flex>
          <Suggestions
            v-model="model"
            :options="{
                placeholder: 'Type Your Name Here'
            }"
            :onInputChange="onInputChange"
            :onItemSelected="onItemSelected"
          >
          </Suggestions>
        </v-flex>
        <v-btn
          class="submit-button"
          :disabled="isLoading"
          :round="false"
          @click="onItemSelected(model)"
          flat>
            <preloader v-if="isLoading" size="20" ></preloader>
            <v-icon v-else>search</v-icon>
        </v-btn>
      </div>
    </v-form>
  </div>
</template>

<script>
import Preloader from './Layout/Preloader';
import Suggestions from 'v-suggestions'
import { searchBinary } from '../services/searchBinary'

export default {
  name: 'NameInput',
  components: { Preloader, Suggestions },
  data() {
    return {
      model: '',
      searchInput: ''
    };
  },
  computed: {
    namesList() {
      return this.$store.state.namesList.list;
    },
    namesListStrings() {
      return this.$store.state.namesList.list.map((item) => item.name);
    },
    isLoading() {
      return this.$store.state.namesList.isLoading;
    }
  },
  methods: {
    onInputChange(query) {
      if (query.trim().length === 0) {
        return null
      }
      let founded = searchBinary(query, this.namesListStrings, true);
      if (founded.length > 5) {
        founded.length = 5;
      }
      return founded;
    },
    onItemSelected(item) {
      let nameObj = this.namesList.find((nameObj) => item.toLowerCase() === nameObj.name.toLowerCase());
      if (nameObj) {
        this.submit(nameObj);
      } else {
        this.submit(item);
      }
    },
    submit(name) {
      setTimeout(() => {
        if (name) {
          this.$emit('submit', name);
          this.$store.commit('setName', name);
        }
      }, 200);
    },
  },
};

</script>

