<template>
  <div class="product-categories-component">
    <Preloader v-if="isLoading" />
    <v-layout row class="mx-1">
      <v-card
        v-for="item in productCategories"
        :key="productCategories.slug"
        :width="`${100 / productCategories.length - 1}%`"
        height="90"
        @click="openDialog(item)"
        class="product-category-card mx-1 "
      >
        <v-img
          :src="item.image"
          contain height="60px" class="mb-1"
        ></v-img>
        {{ item.name }}
      </v-card>
    </v-layout>
    <ModalDialog :dialogIsOpen="dialogIsOpen" @closeDialog="closeDialog">
      <ProductsCategoriesDialog
        @closeDialog="closeDialog"
        :productSubcategories="currentProductSubcategories"
      />
    </ModalDialog>
  </div>
</template>

<script>
import ProductsCategoriesDialog from './ProductsCategoriesDialog';
import Preloader from './Layout/Preloader';
import ModalDialog from './Layout/ModalDialog';

export default {
  name: 'ProductCategories',
  components: {
    Preloader,
    ProductsCategoriesDialog,
    ModalDialog
  },
  data() {
    return {
      model: '',
      currentProductCategory: null,
      dialogIsOpen: false
    }
  },
  created() {
    this.$store.dispatch('fetchIfEmpty', { key: 'productCategories' });
  },
  computed: {
    productCategories() {
      return this.$store.state.productCategories.list.filter((item) => !item.parent);
    },
    currentProductSubcategories() {
      return this.currentProductCategory
        && this.$store.state.productCategories.list.filter((item) => item.parent === this.currentProductCategory.id)
    },
    isLoading() {
      return this.$store.state.productCategories.isLoading;
    }
  },
  methods: {
    openDialog(currentCategory) {
      this.currentProductCategory = currentCategory;
      this.dialogIsOpen = true;
    },

    closeDialog() {
      this.currentProductCategory = null;
      this.dialogIsOpen = false;
    }
  }
};
</script>

<style lang="scss">
  $grey: #838a92aa;
  .product-categories-component {
    .product-category-card {
      cursor: pointer;
      @media screen and (max-width: 600px) {
        font-size: 11px;
      }
    }
    .product-category-card:hover {
      box-shadow: 0 0 5px 3px $grey;
    }
  }
</style>
