<template>
  <div class="product-with-design-component">
    <Preloader size="80" v-if="isLoading"/>
    <div class="image-wrapper" v-else>
      <img
        :src="(currentColorIndex && product.images[currentColorIndex].image) || product.images[0].image"
        class="image-product"
      ></img>
      <v-img
        class="image-logo"
        :src="currentDesign.activeImage"
        :class="{'inverted': invertLogo}"
        :contain="true"
        :style="{
          left: `${product.images[currentColorIndex || 0].left}%`,
          right: `${product.images[currentColorIndex || 0].right}%`,
          top: `${product.images[currentColorIndex || 0].top }%`,
          bottom: `${product.images[currentColorIndex || 0].bottom}%`
          }"
        :position="product.images[currentColorIndex || 0].horizontal_align + ' ' + product.images[currentColorIndex || 0].vertical_align"
        @error="onDesignLoadingError"
      ></v-img>

    </div>
  </div>
</template>

<script>
import Preloader from "./Layout/Preloader";
import VueLoadImage from 'vue-load-image';



export default {
  name: 'ProductWithDesign',
  components: {
    Preloader,
    'vue-load-image': VueLoadImage
  },
  props: ['product', 'design', 'name', 'currentColorIndex', 'currentColorLightness', 'height', 'width'],
  created() {
    this.$store.dispatch('fetchIfEmpty', { key: 'namesList' });
  },
  computed: {
    isLoading() {
      return this.$store.state.namesList.isLoading || !this.design;
    },
    currentDesign() {
      let noImages = this.$store.state.designs.noImages[this.name];
      let namesList = this.$store.state.namesList.list;
      if (!namesList.find(item => item.slug === this.name)) {
        return Object.assign({}, this.design, { activeImage: this.design.image });
      }
      if (noImages && noImages.indexOf(this.design.slug) !== -1) {
        return Object.assign({}, this.design, { activeImage: this.design.image });
      }
      // replace logo default variant with demo, if it wasn't loaded
      return Object.assign({}, this.design, { activeImage: `${this.design.image_url}/${this.name}.png` });
    },
    invertLogo() {
      let designLightness = this.design.lightness;
      let productLightness = this.currentColorLightness === undefined
        ? this.product.variants[0].color.lightness
        : this.currentColorLightness;
      return designLightness !== null
        && productLightness !== null
        && designLightness === productLightness;
    }
  },
  methods: {
    onDesignLoadingError() {
      this.$store.commit('setNoDesignImage', { nameSlug: this.name, designSlug: this.design.slug });
    }
  }
};
</script>

<style lang="scss">
  .product-with-design-component {
    .image-wrapper {
      position: relative;
      display: inline-block;
    }
    .image-logo {
      position: absolute;
    }
    .image-product {
      width: 100%;
    }
    .inverted {
      filter: invert(1);
    }

    .vue-load-image {
      width: 100%;
      height: 100%;
      position: relative;
    }
  }
</style>
