const backend = new Backend();

export default {
  name: 'DialogForWho',
  props: ['model', 'currentStep'],
  components: {
    ForWho,
    ChooseDesignCategory,
    ChooseDesignSubcategory,
    UniqueNameDialog
  },
  methods: {
    onChangeStep(step) {
      this.$emit('changeStep', step);
    }
  }
};
</script>

