<template>
  <tr class="order-item-row-component">
    <td class="text-xs-center ">
      {{item.design}}
      <ProductWithDesign
        :product="product"
        :design="currentDesign"
        :name="item.name"
        :currentColorIndex="currentVariant && currentColorIndex"
        :currentColorLightness="currentVariant && currentVariant.color && currentVariant.color.lightness"
        class="product-img"
      />
    </td>
    <td >
      <p class="title font-weight-bold">{{product.name}}</p>
      <p>
        <span class="grey--text">Name: </span>
        <span class="text-capitalize"> {{ item.name }}</span>
      </p>
      <p v-if="item.design_images.extra_data && item.design_images.extra_data.name">
        <span class="grey--text">Design: </span>
        {{ item.design_images.extra_data.name }}
      </p>
      <p>
        <span class="grey--text">Color:</span>
        {{ currentVariant.color.name }}
      </p>
      <p>
        <span class="grey--text">Size: </span>
        {{ currentVariant.size.name }}
      </p>
    </td>
    <td class=" ">
      {{ item.price }}
    </td>
    <td class=" ">
      {{ item.quantity }}
    </td>
    <td >
      {{ (item.price * item.quantity).toFixed(2) }}
    </td>
  </tr>
</template>
<script>
  import ProductWithDesign from './ProductWithDesign';

  export default {
    name: 'OrderItemRow',
    props: ['item', 'product'],
    components: { ProductWithDesign },
    computed: {
      currentVariant() {
          return this.product.variants.find((item) => item.id === this.item.variant);
      },
      currentDesign() {
        let extra_data;
        try {
          extra_data = JSON.parse(this.item.design_images[0].extra_data)
        } catch {
          extra_data = null;
        }
        return {
            image: `${this.item.design_images[0].image_url}/name.png`,
            image_url: this.item.design_images[0].image_url,
            lightness: extra_data && extra_data.lightness
          };
      },
      currentColorIndex() {
        let colorId = this.currentVariant && this.currentVariant.color.id;
        if (!colorId) return;
        return  this.product.colors.findIndex((item) => item.id === colorId);
      }
    }
  }
</script>
<style lang="scss">
  .order-item-row-component {
    .product-img {
      max-width: 100px;
      min-width: 70px;
    }
  }
</style>
