<template>
  <v-container grid-list-xl class="special-shipping-component">
    <v-stepper value="2" alt-labels>
      <v-stepper-header>
        <v-stepper-step step="1" complete class="text-uppercase">Summary</v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step step="2" class="text-uppercase">Shipping</v-stepper-step>
      </v-stepper-header>
    </v-stepper>

    <v-layout row wrap>
      <v-flex xs12 md8>
        <v-form id="shipping-form" ref="shippingForm" v-model="valid" lazy-validation>
          <v-layout row wrap>
            <h3 class="headline text-uppercase">Shipping information</h3>
            <v-flex xs12>
              <v-text-field
                v-model="form.fullName"
                :rules="rules.fullNameRules"
                label="Full Name"
              ></v-text-field>
            </v-flex>

            <v-flex xs12 sm6>
              <v-text-field
                v-model="form.addressLine1"
                :rules="rules.addressLine1Rules"
                label="Address Line 1"
                required
              ></v-text-field>
            </v-flex>
            <v-flex xs12 sm6>
              <v-text-field
                v-model="form.addressLine2"
                label="Address Line 2"
              ></v-text-field>
            </v-flex>

            <v-flex xs12 sm6>
              <v-text-field
                v-model="form.city"
                :rules="rules.cityRules"
                label="City"
              ></v-text-field>
            </v-flex>
            <v-flex xs12 sm6>
              <v-text-field
                v-model="form.state"
                :rules="rules.stateRules"
                label="State"
              ></v-text-field>
            </v-flex>

            <v-flex xs12 sm6>
              <v-text-field
                v-model="form.postalCode"
                :rules="rules.postalCodeRules"
                label="Postal Code"
              ></v-text-field>
            </v-flex>
            <v-flex xs12 sm6>
              <v-text-field
                v-model="form.country"
                :rules="rules.countryRules"
                label="Country"
              ></v-text-field>
            </v-flex>

            <v-flex xs12 sm6>
              <v-text-field
                v-model="form.phoneNumber"
                :rules="rules.phoneNumberRules"
                label="Phone"
              ></v-text-field>
            </v-flex>
            <v-flex xs12 sm6>
              <v-text-field
                v-model="form.email"
                :rules="rules.emailRules"
                label="Email"
              ></v-text-field>
            </v-flex>

            <h3 class="headline text-uppercase">Shipping Method</h3>
            <v-flex xs12>
              <v-text-field
                v-model="form.shippingMethod"
                :rules="rules.shippingMethodRules"
                label="Please fill your address to see shipping options"
              ></v-text-field>
            </v-flex>

            <h3 class="headline text-uppercase">Coupon</h3>
            <v-flex xs12>
              <v-text-field
                v-model="form.coupon"
                label="Coupon Code"
              ></v-text-field>
            </v-flex>
          </v-layout>

        </v-form>
      </v-flex>

      <v-flex xs12 md4>
        <v-carousel light :cycle="false">
          <v-carousel-item
            v-for="(item,i) in cartItems"
            :key="i"
          >
            <v-layout align-center justify-center fill-height column>
              <ProductWithDesign
                :name="item.name"
                :product="item.product"
                :design="item.design"
                :currentColorIndex="item.currentColorIndex"
                :currentColorLightness="item.productVariant.color.lightness"
                class="product-img"
              />
              <p class="subheading pa-3">{{ item.product.name }}</p>
            </v-layout>
          </v-carousel-item>
        </v-carousel>
      </v-flex>

      <v-flex class="pl-0">
        <v-btn color="info" @click="submit">Submit</v-btn>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import ProductWithDesign from '../components/ProductWithDesign';
import Backend from '../services/backend';

let backend = new Backend();
const formRules = {
  //commented unused rules
  fullNameRules: [ v => !!v || 'Full Name is required' ],
  addressLine1Rules: [ v => !!v || 'Address Line 1 is required'],
  // addressLine2Rules: [],
  cityRules: [ v => !!v || 'City is required' ],
   stateRules: [ v => !!v || 'State is required' ],
   postalCodeRules: [ v => !!v || 'Postal code is required' ],
   countryRules: [ v => !!v || 'Country is required'],
  //phoneNumberRules: [ v => !!v || 'Phone is required' ],
  emailRules: [ v => !!v || 'Email is required' ],
   shippingMethodRules: [ v => !!v || 'Shipping method is required'],
  // couponRules: []
};

export default {
  name: 'SpecialShipping',
  components: { ProductWithDesign },
  data() {
    return {
      valid: false,
      form: {
        fullName: 'Andre',
        addressLine1: '123 Green Str.',
        addressLine2: 'Apt.5',
        city: 'New York',
        state: 'NY',
        postalCode: 34234,
        country: 'USA',
        phoneNumber: '8984424349',
        email: 'andre@gmail.com',
        shippingMethod: 'STANDARD',
        coupon: 'C0129123',
      },
      rules: formRules
    };
  },
  computed: {
    cartItems() {
      return this.$store.state.cart.items;
    },
    total() {
      let total =  this.cartItems.reduce((accumulator, item) =>
          Number((accumulator + item.productVariant.price * item.count).toFixed(2))
        , 0);
      return (total).toFixed(2);
    }
  },
  methods: {
    submit() {
//        return;
      if (!this.$refs.shippingForm.validate()) {
//        let children = this.$refs.shippingForm.$children;
//        for (let i in children) {
//          if (!children[i].valid){
//              children[i].focus();
//              break;
//          }
//        }
        this.$vuetify.goTo(this.$refs.shippingForm);
        return;
      }
      let cartItems = this.$store.state.cart.items;

      let billingAddress = {
        street: this.form.addressLine1,
        apt: this.form.addressLine2,
        city: this.form.city,
        country: this.form.country,
        state: this.form.state,
        postal_code: this.form.postalCode
      };
      let orderToSend = {
        store: 1,
        status: 'P',
        shipping_method: "STANDARD",
        full_name: this.form.fullName,
        email: this.form.email,
        coupon_code: this.form.coupon,
        billing_address: billingAddress,
        shipping_address: billingAddress,
        total: this.total,
        //CHANGE IT
        items:
          cartItems.map((cartItem) => ({
            variant: cartItem.productVariant.id,
            quantity: cartItem.count,
            price: cartItem.productVariant.price,
            name: cartItem.name,
            design_images: [
              {
                image_position: 'D',
                image_id: cartItem.design.id,
                extra_data: JSON.stringify({
                  name: cartItem.design.name,
                  lightness: cartItem.design.lightness
                }),
                lightness: true,
                image_url: cartItem.design.image_url
              }
            ],
          }))
      };

      console.log(orderToSend);
      backend.postOrder(orderToSend).then(this.onSuccess)
        .catch(error => {
            console.log(error && error.response);
        })
    },
    onSuccess(res) {
      this.$store.dispatch('clearCart');
      this.$router.push({ name: 'orders'});
    }
  }
}
</script>

