<template>
  <v-container grid-list-xl class="customize-product-component">
    <preloader v-if="isLoading"></preloader>
    <v-layout v-else row wrap justify-center>
      <v-flex xs8 md6 xl5>
        <v-layout
          row align-center fill-height
          :class="this.$vuetify.breakpoint.mdAndDown ? 'justify-center' : 'justify-end'"
        >
          <v-layout column class="sizes-block font-weight-bold" align-center>
            <v-card
              class="size-item mb-2"
              v-for="item in product.sizes"
              :class="{
                'active': item.name === activeSize.name,
                'disabled': sizeIsDisabled(item),
              }"
              color="#ffffff"
              @click="selectActiveSize(item)"
            >
              <span class="size-text">{{ item.name }}</span>
            </v-card>
          </v-layout>
          <v-card v-if="product.images" flat class="product-block">
            <ProductWithDesign
              :product="product"
              :design="currentDesign"
              :name="$route.params.name"
              :currentColorIndex="currentColor && currentColor.index "
              :currentColorLightness="currentVariant && currentVariant.color && currentVariant.color.lightness"
            />
          </v-card>
        </v-layout>
      </v-flex>

      <v-flex xs11 md6 v-if="currentVariant && currentVariant.size && product">
        <h3 class="product-name headline mb-3 text-uppercase">{{ product.name }}</h3>
        <div class="description subheading">
          <p>
            {{ product.description }}
          </p>
          <v-layout align-center wrap class="colors-block mb-4">
            <v-card
            class="color-item ma-1"
            v-for="(item, index) in product.colors"
            :key="item.slug"
            @click="selectCurrentColor(item, index)"
            :color="item.value"
            :class="{'active': item.value === currentColor.value}"
            >
            </v-card>
          </v-layout>
          <p class="font-weight-bold mb-4">
            Price: $ {{ currentPrice }}
          </p>
          <p>
            <QuantityInput v-model="count"/>
          </p>
          <p>
            <v-btn color="info" @click="addToCart">Add to Cart</v-btn>
          </p>
        </div>
      </v-flex>
    </v-layout>
    <v-dialog v-model="addedToCartDialogIsOpen" max-width="450">
      <AddedToCartDialog :productName="product.name" @closeDialog="addedToCartDialogIsOpen=false"/>
    </v-dialog>
  </v-container>
</template>

<script>
import Preloader from '../components/Layout/Preloader';
import ProductWithDesign from '../components/ProductWithDesign';
import AddedToCartDialog from '../components/AddedToCartDialog';
import QuantityInput from '../components/QuantityInput';
import ModalDialog from '../components/Layout/ModalDialog';
import DialogDesign from '../components/DialogDesign/DialogDesign';

export default {
  name: 'CustomizeProductPage',
  components: {
    Preloader,
    ProductWithDesign,
    AddedToCartDialog,
    QuantityInput,
    ModalDialog,
    DialogDesign
  },
  watch: {
    '$route.params': 'loadData'
  },
  data() {
    return {
      currentColor: {},
      currentVariant: {},
      activeSize: {},
      count: 1,
      addedToCartDialogIsOpen: false
    }
  },
  created() {
    this.loadData();
  },
  updated() {
      if(!this.currentVariant) {
          this.loadData();
      }
  },
  computed: {
    product() {
      return this.$store.state.products.current || {};
    },
    isLoading() {
      return this.$store.state.products.isLoading;
    },
    currentDesign() {
      return this.$store.state.designs.current;
    },
    currentPrice() {
      return this.currentVariant && this.currentVariant.price;
    },
    productCategory() {
        return this.$route.params.productCategory || 3;
    }
  },
  methods: {
    loadData() {
      this.$store.dispatch('fetchIfEmpty', { key: 'namesList' }).then(() => {
        if (!this.$store.state.namesList.current) {
          this.$store.commit('setNameSlug', this.$route.params.name);
        }
      });
      this.$store.dispatch('fetchIfEmpty', { key: 'products', id: this.productCategory, productId: this.$route.params.product })
        .then(() => {
          this.$store.commit('setCurrentProduct', this.$route.params.product);
          this.currentVariant = this.product.variants && this.product.variants[0];
          this.currentColor = this.currentVariant && this.currentVariant.color;
          this.activeSize = this.currentVariant && this.currentVariant.size;
      })

      this.$store.dispatch('fetchIfEmpty', { key: 'designs', id: this.$route.params.subcategory }).then(() => {
        this.$store.commit('setCurrentDesign', this.$route.params.design);
      });
    },
    addToCart() {
      this.$store.dispatch('addCartItem', {
        name: this.$route.params.name,
        design: this.currentDesign,
        count: this.count,
        product: this.product,
        productColor: this.currentColor,
        productSize: this.activeSize,
        productVariant: this.currentVariant,
        currentColorIndex: this.currentColor && this.currentColor.index
      });

      this.addedToCartDialogIsOpen = true;
    },
    sizeIsDisabled(size) {
      if (!this.currentColor || !this.currentVariant) return;
      return !this.product.variants.find(variant => {
        return size.name === variant.size.name && variant.color.name === this.currentColor.name;
      });
    },
    selectCurrentColor(color, index) {
      this.currentColor = Object.assign({}, color, { index: index });
      this.currentVariant = this.product.variants.find((item) =>
      item.color.name === this.currentColor.name && item.size.name === this.activeSize.name
      );
      if (!this.currentVariant) {
        this.currentVariant = this.product.variants.find((item) =>
          item.color.name === this.currentColor.name && item.size.name === this.activeSize.name
        );
        this.activeSize = this.currentVariant.size;
      }
    },
    selectActiveSize(size) {
      if (this.sizeIsDisabled(size)) return;
      this.activeSize = size;
    },
    refreshCurrentSize() {
        this.activeSize = this.product.sizes[0];
//      this.activeSize = this.product.variants.find(value =>
//      value.color.name === this.currentColor.name
//      );
    }
  }
}
</script>

