import Vue from 'vue';
import Vuex from 'vuex';
import Backend from './services/backend';
import createLogger from 'vuex/dist/logger';

const backend = new Backend();

Vue.use(Vuex);


function loadFromLocalStorage(key, defaultValue) {
  let value;
  try {
    value = JSON.parse(localStorage.getItem(key));
  } catch(e) {
    return defaultValue;
  }
  if (!value) return defaultValue;
  return value;
}

function saveCartToLocalStorage(cart) {
  localStorage.setItem('cart', JSON.stringify(cart))
}

function saveNamesToLocalStorage(names) {
  localStorage.setItem('namesList', JSON.stringify(names));
}


export default new Vuex.Store({
  state: {
    namesList: {
      list: loadFromLocalStorage('namesList', []),
      isLoading: false,
      error: null,
      current: null,
      // currentSlug: null
    },
    designCategories: {
      list: [],
      isLoading: false,
      error: null,
      current: null,
      currentSubcategory: null
    },
    designs: {
      list: [],
      isLoading: false,
      error: null,
      current: null,
      designSubcategoryId: null,
      noImages: {}
    },
    productCategories: {
      list: [],
      isLoading: false,
      error: null,
      current: null,
    },
    products: {
      list: [],
      isLoading: false,
      error: null,
      current: null,
      productSubcategoryId: null
    },
    featuredProducts: {
      list: [],
      isLoading: false,
      error: null,
      current: null
    },
    orders: {
      list: [],
      isLoading: false,
      error: null,
      current: null,
    },
    variants: {
      list: {}
    },
    cart: loadFromLocalStorage('cart', { items: [] })
  },
  mutations: {
    setName(state, value) {
      // if name selected from list
      if (value && typeof value === 'object') {
        state.namesList.current = value;
      } // if name is custom
      else {
        state.namesList.current = {
          name: value,
          slug: value.toLowerCase().replace(/ +/g, '-'),
          isUnique: true
        }
      }
    },
    setNameSlug(state, slug) {
      state.namesList.current = state.namesList.list.find((item) => item.slug === slug)
        || { name: slug, slug: slug.toLowerCase().replace(/ +/g, '-'), isUnique: true };
    },

    // DESIGN CATEGORIES
    setDesignCategory(state, value) {
      state.designCategories.current = value;
    },
    setDesignSubcategory(state, value) {
      state.designCategories.currentSubcategory = value;
    },

    // DESIGNS
    setCurrentDesign(state, id) {
      state.designs.current = state.designs.list.find((item) => item.id === parseInt(id));
    },
    setNoDesignImage(state, { nameSlug, designSlug }) {
      let noImages = Object.assign({}, state.designs.noImages);
      if (noImages[nameSlug]) {
        noImages[nameSlug].push(designSlug);
      } else {
        noImages[nameSlug] = [designSlug];
      }
      state.designs.noImages = noImages;
    },

    // PRODUCTS
    setCurrentProduct(state, id) {
      state.products.current = state.products.list.find((item) => item.id === parseInt(id));
    },

    // CART
    // item: {name, design, product, productColor, productSize, count}
    addCartItem(state, item) {
      let cartItems = [].concat(state.cart.items);
      cartItems.push(item);
      state.cart.items = cartItems;
    },
    setItemsToCart(state, items) {
      state.cart.items = items;
    },

    // FETCH
    fetchStart(state, { key, id}) {
      state[key].isLoading = true;
      if (key === 'designs') {
        state.designs.designSubcategoryId = id;
      }
      if (key === 'products') {
        state.products.productSubcategoryId = id;
      }

    },
    fetchSuccess(state, { key, data, id }) {
      state[key].isLoading = false;
      state[key].error = null;
      state[key].list = data;
      if (key === 'designs') {
        state.designs.designSubcategoryId = id;
      }
      if (key === 'namesList') {
        saveNamesToLocalStorage(data);
      }
      if (key === 'products') {
        state.products.productSubcategoryId = id;
        state[key].list.forEach((product) => {
          product.variants.forEach((variant) => {
            state.variants.list[variant.id] = variant;
          })
        });
      }
    },
    fetchError(state, { key, error }) {
      state[key].isLoading = false;
      state[key].error = error;
      if (key === 'designs') {
        state.designs.designSubcategoryId = null;
      }
      if (key === 'products') {
        state.products.productSubcategoryId = null;
      }
    },

  },
  actions: {
    addCartItem({ commit, state }, item) {
      commit('addCartItem', item);
      saveCartToLocalStorage(state.cart);
    },
    deleteCartItem({ commit, state }, item) {
      let filteredItems = state.cart.items.filter(value => value !== item);
      commit('setItemsToCart', filteredItems);
      saveCartToLocalStorage(state.cart);
    },
    setCartItemCount({ commit, state }, { item, count} ) {
      let cart = Object.assign({}, state.cart);
      let filteredItems = cart.items.filter(value => value === item);
      if (filteredItems && filteredItems.length) {
        filteredItems[0].count = count;
      }
      commit('setItemsToCart', cart.items);
      saveCartToLocalStorage(state.cart);
    },
    clearCart({ commit, state }) {
      commit('setItemsToCart', []);
      saveCartToLocalStorage(state.cart);
    },
    fetch({ commit, state }, { key, id }) {
      return new Promise((resolve, reject) => {
        if (state[key].isLoading) return;
        let keyCapitalized = key.replace(/^\w/g, l => l.toUpperCase());
        commit(`fetchStart`, { key, id });
        backend[`get${keyCapitalized}`](id)
          .then(res => {
            commit(`fetchSuccess`, { key, id, data: res.data });
            resolve({ data: res.data });
          })
          .catch(error => {
            commit(`fetchError`, { key, error: handleErrorMessage(error, key) });
            reject({ error: handleErrorMessage(error, key) });
          })
      })
    },

    fetchIfEmpty({ commit, state, dispatch }, { key, id }) {
      if (key === 'designs' && state.designs.designSubcategoryId === id) return;
      // if (key === 'products' && state.products.productSubcategoryId === id) return;
      if (!state[key].list || !state[key].list.length || key === 'designs') {
        return dispatch('fetch', { key, id });
      }
    }
  },

  getters: {
    designSubcategories: state => {
      const currentCategoryId = state.designCategories.current && state.designCategories.current.id;
      if (!currentCategoryId) return [];
      return state.designCategories.list.filter((item) => item.parent === currentCategoryId);
    },
    featuredProductsList: state => {
      const list = state.featuredProducts.list.map((item) => {
        item.product.images = [item.image];
        item.design.lightness = item.design.image.lightness;
        item.design.image = item.design.image.url;
        delete item.image;
        return item;
      });
      return list;
    },
    designCategories: state => {
      return state.designCategories.list.filter((item) => item.parent === null);
    },
},
//plugins: [createLogger()]
});

function handleErrorMessage(error, key) {
  return error.response && error.response.data &&
    JSON.stringify(error.response.data).replace(/[[\]{}]+/g, '') ||
    `Something went wrong in ${key}. Please try again later`;
}
