<template>
  <v-container class="orders-page-component">
    <h3 class="text-uppercase headline text-xs-center mb-4">Orders</h3>
    <v-layout v-if="isLoading" justify-center row class="mt-4">
      <Preloader size="80"/>
    </v-layout>
    <v-card v-else v-for="order in orders" class="order-item" >
      <v-layout row class="subheading">
        <v-flex xs5>
          <p class="font-weight-bold">Full name: </p>
        </v-flex>
        <p >{{order.full_name}}</p>

      </v-layout>
      <v-layout row class="subheading">
        <v-flex xs5>
          <p class="font-weight-bold">E-mail: </p>
        </v-flex>
        <p >{{order.email}}</p>

      </v-layout>
      <v-layout row class="subheading">
        <v-flex xs5>
          <p class="font-weight-bold">Order status: </p>
        </v-flex>
        <p >{{order.status}}</p>

      </v-layout>
      <!--<div v-for="pack in order.packages" class="mb-4">-->
        <v-layout row class="subheading">
          <v-flex xs5>
            <p class="font-weight-bold">Address: </p>
          </v-flex>
            <p>{{
              `${order.shipping_address.street} ${order.shipping_address.apt ? order.shipping_address.apt : ''},
                ${order.shipping_address.city} ${order.shipping_address.state} ${order.shipping_address.postal_code},
                ${order.shipping_address.country}`
              }}</p>
        </v-layout>
        <v-layout row class="subheading">
          <v-flex xs5>
            <p class="font-weight-bold">Total price: </p>
          </v-flex>
            <p>$ {{order.total}}</p>
        </v-layout>

        <v-data-table
          :headers="headers"
          :items="order.items"
          hide-actions
          :class="{ 'mx-5' : $vuetify.breakpoint.smAndUp }"
        >
          <template slot="headers" slot-scope="props">
            <th v-for="item in props.headers" class="text-xs-left">
              {{ item.text }}
            </th>
          </template>
          <template slot="items" slot-scope="props">
            <OrderItemRow :item="props.item" :product="getProduct(props.item.variant)"/>
          </template>
        </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
import Backend from '../services/backend';
import ProductWithDesign from "../components/ProductWithDesign";
import QuantityInput from '../components/QuantityInput';
import Preloader from '../components/Layout/Preloader';
import OrderItemRow from '../components/OrderItemRow';

const backend = new Backend();
const headers = [
  { text: '' },
  { text: 'Item' },
  { text: 'Price' },
  { text: 'Quantity' },
  { text: 'Total' }
];

export default {
  name: 'OrdersPage',
  components: { ProductWithDesign, QuantityInput, Preloader, OrderItemRow },
  data() {
    return {
      headers: headers,
    };
  },
  created() {
    this.loadData();
  },
  computed: {
    isLoading() {
      return this.$store.state.orders.isLoading || this.$store.state.products.isLoading;
    },
    orders() {
      return this.$store.state.orders.list;
    },
    products() {
      return this.$store.state.products.list;
    },
    variants() {
      return this.$store.state.variants.list;
    }
  },
  methods: {
    deleteCartItem(item) {
      this.$store.dispatch('deleteCartItem', item);
    },
    setItemCount(item, count) {
      this.$store.dispatch('setCartItemCount', { item, count });
    },
    loadData() {
      this.$store.dispatch('fetchIfEmpty', { key: 'orders' });
      this.$store.dispatch('fetchIfEmpty', { key: 'products' });
    },
    getProduct(variantId) {
      let productId = this.variants[variantId].product;
      return this.$store.state.products.list.find((item) => item.id === productId);
    }
  }
}
</script>

